import React, { Component } from "react";
import Chart from "chart.js/auto";
import InputCalendar from "../../components/common/InputCalendar";
import ButtonDefault from "../../components/common/buttons/ButtonDefault";
import GroupRange from "../../components/common/button_group/GroupRange"
import DateConverter from "../DateConverter";
import AuthZab from "../AuthZab";
import Cookies from "js-cookie";
class Graph extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: props.data
        };
        this.chartRef = React.createRef();
    }
    componentDidMount() {
        this.paintGraph();
    }

    componentDidUpdate(prevProps, prevState) {
        //          X
        if (prevProps.data !== this.props.data || prevProps.from !== this.props.from || prevProps.to !== this.props.to) {
            this.setState({
                data: this.props.data,
                from: this.props.from,
                to: this.props.to,
            }, this.paintGraph); //  paintGraph   
        }
    }
    cancel_runprocess(){
        if (this.updateTaskId) {
            clearInterval(this.updateTaskId);            
        }
    }
    runAutoUpdate(){
        this.cancel_runprocess();
        this.updateTaskId = setInterval(()=>{
            console.log(1);
            const from = DateConverter.getSubtractDates(new Date(), this.range); // data -> FORMAT [0,0,0,0,0,0]
            const dateFrom = DateConverter.getSeconds(from);
            const dateTo = DateConverter.getSeconds(new Date());
            AuthZab.getHistory(
                Cookies.get("itemType"),
                Cookies.get("itemId"),
                dateFrom,
                dateTo
            ).then(response => {
                if (response) {
                    console.log(response);
                    this.setState({
                        data: response
                    });
                    this.componentDidMount();
                }
            })
            .catch(error => {
                console.error(" :", error);
            });
        },1000)
    }

    paintGraph() {
        
        const { data } = this.state;
        const chartRef = this.chartRef.current.getContext("2d");

        if (typeof this.chart !== "undefined") {
            this.chart.destroy(); //   ,   
        }

        const datas = {
            datasets: [
                {
                    label: "this.name",
                    data: data.map(item => {
                        // alert(typeof parseInt(item.clock))
                        // let time = DateConverter.getStringFormatDate(DateConverter.getCurrentDate(Number(item.clock)));
                        // const date = new Date(parseInt(item.clock) * 1000); //   1000,   Date  

                        // const year = date.getFullYear();
                        // const month = String(date.getMonth() + 1).padStart(2, '0'); //   ,   < 10
                        // const day = String(date.getDate()).padStart(2, '0'); //   ,   < 10
                        // const hours = String(date.getHours()).padStart(2, '0');
                        // const minutes = String(date.getMinutes()).padStart(2, '0');
                        // const seconds = String(date.getSeconds()).padStart(2, '0');

                        // const formattedDate = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
                        // let t = toString(item.clock);
                        // console.log(typeof item.clock)
                        return {
                            x: item.clock,
                            y: item.value
                        };
                    }),
                    
                    borderColor: 'red',
                    backgroundColor: 'rgba(255, 0, 0, 0.5)',
                }
            ]
        };
        
        this.chart = new Chart(chartRef, {
            type: 'line',
            data: datas,
            options: {
                responsive: true,
                hover: {mode: null},
                events: [],
                animation: {
                    //  
                    duration: 0
                },
                scales: {
                    y: {
                        type: 'linear',
                        beginAtZero: false,
                        stepSize: 50,
                        max: null
                    },
                    x: {
                        type: 'linear',
                        position: 'bottom',
                        min: this.from,
                        max: this.to,
                        
                    }
                }
            }
        });
    }

    render() {
        return (
            <div>
            <form>
                <div class="date">
                    {/* <InputCalendar/> */}
                    <div class="date_layout">
                    <GroupRange onButtonClick={(data) => {
                        console.log(data)
                        this.range = data;
                        this.runAutoUpdate();
                        // const from = DateConverter.getSubtractDates(new Date(), data); // data -> FORMAT [0,0,0,0,0,0]
                        // const dateFrom = DateConverter.getSeconds(from);
                        // const dateTo = DateConverter.getSeconds(new Date());
                        // AuthZab.getHistory(
                        //     Cookies.get("itemType"),
                        //     Cookies.get("itemId"),
                        //     dateFrom,
                        //     dateTo
                        // ).then(response => {
                        //     if (response) {
                        //         // console.log(response);
                        //         this.setState({
                        //             data: response
                        //         });
                        //         this.componentDidMount();
                        //     }
                        // })
                        // .catch(error => {
                        //     console.error(" :", error);
                        // });
                    }} />
                    </div>
                </div>
            </form>
            <canvas ref={this.chartRef} />
            </div>
        );
    }
}

export default Graph;
