// ButtonGroup.js
import React from 'react';
import ButtonDateRange from '../buttons/ButtonDateRange';
function ButtonGroup({ onButtonClick }) {
  const buttons = [
    { text: " 5 ", data: [0, 0, 0, 0, 0, 5] },
    { text: " 5 ", data: [0, 0, 0, 0, 5, 0] },
    { text: " 15 ", data: [0, 0, 0, 0, 15, 0] },
    { text: " 30 ", data: [0, 0, 0, 0, 30, 0] },
    { text: " 1 ", data: [0, 0, 0, 1, 0, 0] },
    { text: " 3 ", data: [0, 0, 0, 3, 0, 0] },
    { text: " 6 ", data: [0, 0, 0, 6, 0, 0] },
    { text: " 12 ", data: [0, 0, 0, 12, 0, 0] },
    { text: " 1 ", data: [0, 0, 1, 0, 0, 0] }
  ];

  return (
    <div>
      {buttons.map((button, index) => (
        <ButtonDateRange
          key={index}
          text={button.text}
          data={button.data}
          onClick={() => onButtonClick(button.data)}
        />
      ))}
    </div>
  );
}

export default ButtonGroup;
